import * as turf from "@turf/turf";

const getPointOnPolyline = (coordinates, targetPoint) => {
  const point = turf.point(targetPoint);
  const line = turf.lineString(coordinates);
  const snapped = turf.pointOnLine(line, point);

  return {
    lat: snapped.geometry.coordinates[1],
    lng: snapped.geometry.coordinates[0],
  };
};

// Helper para transformar route em Promise
const routePromise = (directionsService, request) =>
  new Promise((resolve, reject) => {
    directionsService.route(request, (result, status) => {
      if (status === "OK") resolve(result);
      else resolve(null); // resolve null em vez de rejeitar
    });
  });

const findNearByPTP = async (coordinates, point, callback) => {
  if (!coordinates || !point) {
    console.error("findNearByPTP: parâmetros inválidos", { coordinates, point });
    callback([]);
    return;
  }

  const pointOnLine = getPointOnPolyline(coordinates, [point.lng, point.lat]);
  const directionsService = new window.google.maps.DirectionsService();

  const initialRequest = {
    origin: new window.google.maps.LatLng(pointOnLine.lat, pointOnLine.lng),
    destination: point,
    travelMode: "WALKING",
  };

  const initialResult = await routePromise(directionsService, initialRequest);

  if (!initialResult) {
    toast.error("Não foi possível calcular a rota PTP");
    callback([]);
    return;
  }

  const allSteps = initialResult.routes[0].legs[0].steps;
  let shortestDistance = +Infinity;
  let shortestPath = null;

  for (let i = 0, j = 0; i < allSteps.length && j < 2; i++, j++) {
    const step = allSteps[i];
    const { lat, lng } = getPointOnPolyline(coordinates, [
      step.start_location.lng(),
      step.start_location.lat(),
    ]);

    const stepRequest = {
      origin: new window.google.maps.LatLng(lat, lng),
      destination: point,
      travelMode: "WALKING",
    };

    const stepResult = await routePromise(directionsService, stepRequest);

    if (stepResult) {
      const distance = stepResult.routes[0].legs[0].distance.value;
      if (distance < shortestDistance) {
        shortestDistance = distance;
        shortestPath = stepResult;
      }
    }
  }

  if (!shortestPath) {
    toast.error("Nenhum caminho válido encontrado");
    callback([]);
    return;
  }

  const path = shortestPath.routes[0].overview_path;
  const { lat, lng } = getPointOnPolyline(coordinates, [path[0].lng(), path[0].lat()]);
  const startPoint = new window.google.maps.LatLng(lat, lng);
  const endPoint = new window.google.maps.LatLng(point);

  const finalResult = [startPoint, ...path, endPoint];
  callback(finalResult);
};

export default findNearByPTP;
